import React, { FC, useEffect } from 'react';
import { useForm } from 'react-hook-form';
import { useDispatch, useSelector } from 'react-redux';
import { RouteComponentProps } from 'react-router';
import { Col, Form, FormGroup, Row } from 'reactstrap';
import {
  ACTION_EVENTS,
  alertManager,
  AlertProps,
  AlertType,
  dispatchClose,
  EditViewProps,
  ErroList,
  ERROR,
  InputGroupInline,
  InputType,
  ProcessBar,
  showToast,
  WindowComponent,
} from 'summer';

import { hasEmpresa } from '../../../../dependencies/empresaDependency';
import { hasEmpresaMFP } from '../../../../dependencies/empresaMFPDependency';
import { EmpresaMFP } from '../../../../models/dtos/mfp/empresaMFP';
import { ManadParam } from '../../../../models/dtos/mfp/manadParam';
import { CENTRALIZACAO_MANAD_ENUM_VALUES, SEM_CENTRALIZACAO } from '../../../../models/enumerated/mfp/centralizacaoManadEnum';
import { FINALIDADE_MANAD_ENUM_VALUES, SOLICITACAO_AUDITOR } from '../../../../models/enumerated/mfp/finalidadeManadEnum';
import { gerarManad } from '../../../../services/mfp.service';
import { Reducers } from '../../../../store/ducks';
import { GlobalState, toggleHeaderDrawer } from '../../../../store/ducks/global.duck';
import EmpresaIS from '../../../components/ger/empresaIS/EmpresaIS';

interface ExportacaoManadProcessViewProps extends EditViewProps<ManadParam> {}

const ExportacaoManadProcessView: FC<ExportacaoManadProcessViewProps & RouteComponentProps> = props => {
  const globalReducer = useSelector<Reducers, GlobalState>(state => state.globalReducer);
  const { ...formProps } = useForm<ManadParam>({ mode: 'onBlur' });
  const { headerDrawerToggle } = useSelector<Reducers, GlobalState>(state => state.globalReducer);
  const dispatch = useDispatch();

  useEffect(
    () => {
      if (globalReducer.globalParameter.empresaMFP !== null) {
        const { usuario } = globalReducer.globalParameter;
        const empresaMFP: EmpresaMFP = globalReducer.globalParameter.empresaMFP;
        setTimeout(() => {
          formProps.setValue('centralizacao', SEM_CENTRALIZACAO.value, true);
          formProps.setValue('finalidade', SOLICITACAO_AUDITOR.value, true);
          formProps.setValue('tipoResponsavel', empresaMFP.tipoResponsavelManad, true);
          formProps.setValue('usuario.id', usuario.id, true);
        }, 100);
      }
    }, // eslint-disable-next-line react-hooks/exhaustive-deps
    []
  );

  const onClose = (goBack: boolean = true) => {
    dispatchClose(props, props.history, goBack);
  };

  const handleCancel = () => onClose();

  const handleExport = (data: ManadParam) => {
    const erros = validateExtras();
    if (erros.length > 0) {
      const params: AlertProps = {
        message: <ErroList erros={erros} />,
        type: AlertType.WARNING,
      };
      alertManager.emit(ACTION_EVENTS.SHOW, params);
    } else {
      callExport(data);
    }
  };

  function callExport(data: ManadParam): void {
    gerarManad(data, {
      errorFunction: mensagem => {
        showToast(mensagem, ERROR);
      },
      thenFunction: () => {
        if (!headerDrawerToggle) {
          dispatch(toggleHeaderDrawer());
        }
      },
    });
  }

  function validateExtras(): string[] {
    const erros = [];

    if (formProps.getValues().refInicial > formProps.getValues().refFinal) {
      erros.push('A Ref. Inicial não pode ser posterior Ref. Final.');
    }

    return erros;
  }

  return (
    <WindowComponent
      title="Exportação - MANAD"
      useGlobalParameter={true}
      globalParameter={globalReducer.globalParameter}
      dependencies={[() => hasEmpresa(globalReducer.globalParameter), () => hasEmpresaMFP(globalReducer.globalParameter)]}
      wikiId={2249}
    >
      <Form>
        <InputGroupInline formProps={formProps} id="tipoResponsavel" hidden={true} />
        <InputGroupInline formProps={formProps} id="usuario.id" hidden={true} />
        <FormGroup>
          <Row>
            <Col>
              <EmpresaIS
                label="Empresa"
                placeholder="Selecione uma empresa."
                id="empresa"
                isPrependLabel={true}
                formProps={{ ...formProps, validation: { required: true } }}
              />
            </Col>
          </Row>
          <Row>
            <Col>
              <InputGroupInline
                formProps={{ ...formProps, validation: { required: true } }}
                id="refInicial"
                placeholder="Informe a Referência Inicial."
                isPrependLabel={true}
                label="Referência Inicial"
                type={InputType.REFERENCE}
              />
            </Col>
            <Col>
              <InputGroupInline
                formProps={{ ...formProps, validation: { required: true } }}
                id="refFinal"
                placeholder="Informe a Referência Final."
                isPrependLabel={true}
                label="Referência Final"
                type={InputType.REFERENCE}
              />
            </Col>
          </Row>
          <Row>
            <Col>
              <InputGroupInline
                formProps={{ ...formProps, validation: { required: true } }}
                id="centralizacao"
                inputDropDownProps={{ optionsProvider: CENTRALIZACAO_MANAD_ENUM_VALUES }}
                isPrependLabel={true}
                label="Centralização"
                placeholder="Informe a Centralização"
                type={InputType.DROP_DOWN}
              />
            </Col>
          </Row>
          <Row>
            <Col>
              <InputGroupInline
                formProps={{ ...formProps, validation: { required: true } }}
                id="finalidade"
                inputDropDownProps={{ optionsProvider: FINALIDADE_MANAD_ENUM_VALUES }}
                isPrependLabel={true}
                label="Finalidade"
                placeholder="Informe a Finalidade"
                type={InputType.DROP_DOWN}
              />
            </Col>
          </Row>
        </FormGroup>
        <ProcessBar handleSubmit={formProps.handleSubmit(handleExport)} handleClose={handleCancel} />
      </Form>
    </WindowComponent>
  );
};

export default ExportacaoManadProcessView;
